let pass = 'DCTP';

let encodePassword = {
    // '0a1f35'   [0x0a, 0x1f, 0x35]
    hexToByte(hexStr) {
        let result = [], t;
        for (let i = 0; i < hexStr.length; i += 2) {
            t = hexStr.substr(i, 2);
            t = parseInt(t, 16);
            result.push(t);
        }
        return result;
    },
    // [0x0a, 0x1f, 0x35]   '0a1f35'
    byteToHex(bytes) {
        let result = [], t;
        for (let i = 0; i < bytes.length; i++) {
            t = bytes[i].toString(16).toUpperCase();
            if (t.length < 2){
                t = '0' + t;
            }
            result.push(t);
        }
        return result.join('');
    },
    // 'abcdefg'   [0x61, 0x62, 0x63, 0x64, 0x65, 0x66, 0x67]
    strToByte(str) {
        let result = [], t;
        for(let i = 0; i < str.length; i++){
            t = str.charCodeAt(i);
            result.push(t);
        }
        return result;
    },
    // [0x61, 0x62, 0x63, 0x64, 0x65, 0x66, 0x67]  'abcdefg'
    byteToStr(bytes) {
        let result = [], t;
        for (let i = 0; i < bytes.length; i++){
            t = String.fromCharCode(bytes[i]);
            result.push(t);
        }
        return result.join('');
    },
    xor(bytes1, bytes2) {
        let result = [], h1, h2;
        for (let i = 0; i < bytes1.length; i++){
            h1 = bytes1[i];
            h2 = bytes2[i] || 0;
            result.push(h1 ^ h2);
        }
        return result;
    },
    encrypt(text) {
        let bt, bp;
        bt = this.strToByte(text);
        bp = this.strToByte(pass);
        return this.byteToHex(this.xor(bt, bp));
    },
    decrypt(enc) {
        let he = this.hexToByte(enc),
            hp = this.strToByte(pass),
            hr = this.xor(he, hp);
        return this.byteToStr(hr);
    }
};

export default  encodePassword;


