import fetch from '@/utils/fetch'

export function getCurrency(query) {
    return fetch({
        url: 'admin/currencytrans',
        method: 'get',
        params: query
    })
}

export function passCurrency(query) {
    return fetch({
        url: 'admin/currencytrans/pass',
        method: 'put',
        data: query
    })
}

export function lockCurrency(query) {
    return fetch({
        url: 'admin/currencytrans/lock',
        method: 'put',
        data: query
    })
}

export function issueCurrency(query) {
    return fetch({
        url: 'admin/currencytrans/issue',
        method: 'put',
        data: query
    })
}

export function cancelCurrency(query) {
    return fetch({
        url: 'admin/currencytrans/cancel',
        method: 'put',
        data: query
    })
}

export function neglectcurrency(query) {
    return fetch({
        url: 'admin/currencytrans/neglect',
        method: 'put',
        data: query
    })
}

export function repairTXId(data) {
    return fetch({
        url: 'admin/currencytrans/repair/txid',
        method: 'put',
        data: data
    })
}




