<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom:20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、登录名" @keyup.native.enter="conditionQuery"
                      v-model="currencyTrans.keyword">
            </el-input>
            <span>虚拟币类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.introduceId"
                       placeholder="全部类型">
                <el-option v-for="coin in coinList" :key="coin.baseSeq" :label="coin.chineseName"
                           :value="coin.baseSeq">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="conditionQuery" size="small" style="margin-left: 30px">查询
            </el-button>
            <el-button type="primary" @click="exportData" size="small" style="margin-left: 20px">导出数据
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label="操作"
                        width="200">
                    <template scope="scope" class="filter-operation">
                        <ul style="list-style: none;padding: 0px;height: 30px;margin: 0px">
                            <li style="float:right;width: 60px"
                                v-if="scope.row.accountStatus!==6">
                                <el-button
                                        type="text"
                                        size="small"
                                        @click="neglectCurrency(scope.row.id,3)">忽略(已转)
                                </el-button>
                            </li>
                            <li style="float:right;width: 60px">
                                <el-button
                                        type="text"
                                        size="small"
                                        @click="passCurrency(scope.row,3)">重新拔币
                                </el-button>
                            </li>
                            <li style="float:right;width: 60px">
                                <el-button
                                        type="text"
                                        size="small"
                                        @click="showRepairTXIdDialog(scope.row.id)">补录ID
                                </el-button>
                            </li>
                        </ul>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="currencyTrans.page"
                               :page-sizes="[10,20,30, 50]" :page-size="currencyTrans.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
        <el-dialog title="请输入密码" :visible.sync="dialogFormVisible" custom-class="shenhe">
            <el-form :model="lockWithdrawalVo">
                <el-form-item>
                    <el-input v-model="lockWithdrawalVo.walletPassword" type="password"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="text-center">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="Currency">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="请输入交易ID" :visible.sync="dialogTXIVisible">
            <el-form :model="lockWithdrawalVo">
                <el-form-item>
                    <el-input v-model="lockWithdrawalVo.txId"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="text-center">
                <el-button @click="dialogTXIVisible = false">取 消</el-button>
                <el-button type="primary" @click="repairTXId">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import encodePassword from '@/utils/encode-password';
    import {getCurrency, passCurrency, neglectcurrency, repairTXId} from '@/api/coinassets';
    //启用禁用筛选条件
    const typeScope = [
        {key: 1, display_name: '充值'},
        {key: 2, display_name: '提现'},
        {key: 3, display_name: '提现'},
    ];
    const allCoin = {baseSeq: 0, chineseName: '全部类型', englishName: ""};
    const statuScope = [
        {key: 0, display_name: '全部状态'},
        {key: 2, display_name: '等待审核'},
        {key: 3, display_name: '成功提现'},
        {key: 4, display_name: '系统撤销'},
        {key: 5, display_name: '用户撤销'},
        {key: 6, display_name: '正在处理'},
        {key: 7, display_name: '正在拨币'},
        {key: 8, display_name: '拨币异常'}
    ];

    export default {
        data() {
            return {
                //搜索需求数据等
                allCoin,
                typeScope,
                dialogFormVisible: false,
                dialogTXIVisible: false,
                coinList: [],
                dateTime: '',
                tableData: [],
                total: null,
                lockWithdrawalVo: {
                    id: '',
                    itype: '',
                    status: '',
                    walletPassword: '',
                    txId: ''
                },

                currencyTrans: {
                    keyword: '',
                    type: 2,
                    status: 8,
                    introduceId: 0,
                    page: 1,
                    pageSize: 15
                },
                msg: '',
                listLoading: true,
                tableHeader: [
                    {
                        label: "单号id",
                        prop: "id",
                        align: 'center',
                        width: 45
                    }, {
                        label: "会员UID",
                        prop: "userId",
                        align: 'center',
                        width: 55
                    }, {
                        label: "登录名",
                        prop: "signtype",
                        width: 85,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? row.phone : (value === 2 ? row.email : '未知');
                        }
                    }, {
                        label: "会员昵称",
                        prop: "nickName",
                        width: 65
                    }, {
                        label: "真实姓名",
                        prop: "realName",
                        width: 65
                    }, {
                        label: "资产类型",
                        prop: "currencyName",
                        width: 65,
                        align: 'center'
                    }, {
                        label: "交易类型",
                        prop: "type",
                        width: 65,
                        align: 'center',
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(typeScope, value, 'key');
                            return item ? item.display_name : '';
                        }
                    }, {
                        label: "状态",
                        prop: "status",
                        width: 65,
                        align: 'center',
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(statuScope, value, 'key');
                            return item ? item.display_name : '';
                        }
                    }, {
                        label: "提现数量",
                        width: 65,
                        align: 'center',
                        formatter(row, column, value) {
                            var r1, r2, m, n;
                            try { r1 = row.amount.toString().split(".")[1].length } catch (e) { r1 = 0 }
                            try { r2 = row.fee.toString().split(".")[1].length } catch (e) { r2 = 0 }
                            m = Math.pow(10, Math.max(r1, r2));
                            n = (r1 >= r2) ? r1 : r2;
                            return ((row.amount * m - row.fee * m) / m).toFixed(n);
                        }
                    }, {
                        label: "手续费",
                        prop: "fee",
                        align: 'center',
                        width: 65
                    }, {
                        label: "提现地址",
                        prop: "transAddress",
                        width: 265,
                        align: 'center'
                    }, {
                        label: "交易id",
                        prop: "txId",
                        width: 450,
                        align: 'center'
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "最后修改时间",
                        prop: "updateTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }]
            }
        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {


            //获取当前页数
            userPage(val) {
                this.currencyTrans.page = val;
                this.getList();
            }
            ,
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.pageSize = val;
                this.getList();
            }

            ,
            //条件查询方法
            conditionQuery() {
                this.getList();
            },
            /**
             * 忽略拨币方法
             * @param id
             */
            neglectCurrency(id, status) {
                this.lockWithdrawalVo.id = id;
                this.lockWithdrawalVo.status = status;
                this.lockWithdrawalVo.itype = 2;
                neglectcurrency(this.lockWithdrawalVo).then(response => {
                    if (response.result) {
                        this.msg = "已经忽略拨币";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            },
            /**
             * 请求列表方法
             */
            getList() {
                this.listLoading = true;
                getCurrency(this.currencyTrans).then(response => {
                    this.tableData = response.data.list;
                    this.total = response.data.totalItems;
                    this.coinList = response.data.coinList;
                    this.coinList.unshift(this.allCoin);
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            },
            //重新拨币
            passCurrency(row, status) {
                this.lockWithdrawalVo.id = row.id;
                this.lockWithdrawalVo.status = status;
                this.lockWithdrawalVo.walletPassword = "";
                this.dialogFormVisible = true;
                /*if (null === row.walletPassword || "" === row.walletPassword) {
                    this.lockWithdrawalVo.walletPassword = "";
                    this.dialogFormVisible = true;
                } else {
                    this.lockWithdrawalVo.walletPassword = row.walletPassword;
                    this.Currency();
                }*/
            },
            Currency() {
                let lockWithdrawal = {
                    id: this.lockWithdrawalVo.id,
                    status: this.lockWithdrawalVo.status,
                    walletPassword: this.lockWithdrawalVo.walletPassword !== ""? encodePassword.encrypt(this.lockWithdrawalVo.walletPassword):"",
                };
                passCurrency(lockWithdrawal).then(response => {
                    if (response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.dialogFormVisible = false;
                    } else {
                        this.msg = response.msg;
                        this.lockWithdrawalVo.walletPassword = "";
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                    this.getList();

                })
            },
            exportData() {
                let url = 'api/admin/currencytrans/nofilter/export?type=2&keyword=' + this.currencyTrans.keyword + '&introduceId=' + this.currencyTrans.introduceId + '&status=' + this.currencyTrans.status;
                let excelurl = '';

                if (this.dateTime[0] === undefined || this.dateTime[0] === null) {
                    this.currencyTrans.endTime = '';
                    this.currencyTrans.startTime = '';
                    excelurl = url ;
                } else {
                    excelurl = url + '&startTime=' + this.currencyTrans.startTime + '&endTime=' + this.currencyTrans.endTime;
                }
                location.href = excelurl;
            },
            showRepairTXIdDialog(id) {
                this.dialogTXIVisible = true;
                this.lockWithdrawalVo.txId = '';
                this.lockWithdrawalVo.id = id;
            },
            repairTXId() {
                repairTXId(this.lockWithdrawalVo).then(response => {
                    if (response.result) {
                        this.getList();
                        this.dialogTXIVisible = false;
                    }
                    this.getMessage(response.msg,response.result);
                })
            },
        }
        ,
        created() {
            this.getList();
        }
    }
</script>



